import './App.css';
import { createTheme, responsiveFontSizes, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import TwitterIcon from '@mui/icons-material/Twitter';
import Typography from '@mui/material/Typography';
import logo from "./assets/images/godroll200.png";
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Link from '@mui/material/Link';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import ReactPlayer from 'react-player'

let theme = createTheme({
  palette: {
    typography: {
      h1: {
        fontWeight:'900'
      },
    },
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#000000',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
    secondary: {
      light: '#0066ff',
      main: '#0044ff',
      // dark: will be calculated from palette.secondary.main,
      contrastText: '#ffcc00',
    },
    // Used by `getContrastText()` to maximize the contrast between
    // the background and the text.
    contrastThreshold: 3,
    // Used by the functions below to shift a color's luminance by approximately
    // two indexes within its tonal palette.
    // E.g., shift from Red 500 to Red 300 or Red 700.
    tonalOffset: 0.2,
  },
});

theme = responsiveFontSizes(theme);

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="App">
        <AppBar elevation={1}  position="static" sx={{backgroundColor:"#ffffff", opacity: 0.95}}>
          <Container maxWidth="lg">
          <Toolbar>
            <img src={logo} alt="Godroll Logo" sx={{ flexGrow: 1 }} />
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>

             </Typography>
             <IconButton
                  size="large"
                  aria-label="show more"
                  color="primary"
                >
                  <TwitterIcon />
                </IconButton>
          </Toolbar>
          </Container>
        </AppBar>
        <main>
          {/* Hero unit */}
          <Box
          sx={{
            bgcolor: '#FAFAFA',
            pt: 8,
            pb: 6,
          }}
          >
            <Container align="center" maxWidth="md"
            sx={{
              pt: 4,
              pb: 6,
            }}>
              <Typography

                variant="h1"
                align="center"
                color="text.primary"
                gutterBottom
                sx={{
                  fontWeight: 900,
                  backgroundClip: 'text',
                  textFillColor: "transparent",
                  backgroundImage: 'linear-gradient(90deg, #007CF0 20%, #00DFD8 80%)',
                }}
              >
                QAULITY. CREATIVE. COMMUNITY.
              </Typography>
              <Typography variant="h5" align="center" color="text.secondary" maxWidth="sm">
                <p>GodRoll builds Solana based projects, that unite, excite, and build the community.</p>
                <p>We believe every piece you recieve should be of quailty and depth (aka: a God Roll).</p>
              </Typography>
            </Container>

            <Container maxWidth="md"
            sx={{
              pt: 6,
              pb: 3,
            }}>
              <Paper elevation={1}  sx={{mb: 3, p:3}}>
                <Grid container spacing={4}>
                  <Grid item xs={12} sm={12} md={6}>
                    <Container sx={{alignItems: "center" }}>
                        <Typography gutterBottom variant="h3"

                        align="center"
                        color="text.primary"
                        gutterBottom
                        sx={{
                          fontWeight: 900,
                        }}>
                          JustShapes
                        </ Typography>
                        <Typography color="text.secondary" variant="p">
                          <p>Simple shapes flowing in perfect loops, that <strong>entrace your brain.</strong></p>
                          <p><strong>Product Type:</strong> Generative Art</p>
                          <p><strong>Status:</strong> In Development</p>
                        </ Typography>

                      </ Container>
                  </ Grid>
                  <Grid item xs={12} sm={12} md={6}>
                    <Card variant="outlined">
                      <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={'https://ss.simplestuff.co.nz/wp-content/uploads/2021/09/red.mp4'} type="video/mp4" />
                    </Card>
                  </ Grid>
                </ Grid>
              </Paper>
            </ Container>

            <Container maxWidth="md"
            sx={{
              pt: 3,
              pb: 6,
            }}>
              <Paper elevation={1}  sx={{mb: 3, p:3}}>
                <Grid container spacing={4}>
                  <Grid item xs={12} sm={12} md={6}>
                    <Container sx={{alignItems: "center" }}>
                        <Typography gutterBottom variant="h3"

                        align="center"
                        color="text.primary"
                        gutterBottom
                        sx={{
                          fontWeight: 900,
                        }}>
                          SolSilver
                        </ Typography>
                        <Typography color="text.secondary" variant="p">
                          <p>Simple shapes flowing in perfect loops, that <strong>entrace your brain.</strong></p>
                          <p><strong>Product Type:</strong> IRL Redemable Silver Coin.</p>
                          <p><strong>Status:</strong> In Development</p>
                        </ Typography>

                      </ Container>
                  </ Grid>
                  <Grid item xs={12} sm={12} md={6}>
                    <Card variant="outlined">
                      <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={'https://ss.simplestuff.co.nz/wp-content/uploads/2021/09/test-coin.mp4'} type="video/mp4" />
                    </Card>
                  </ Grid>
                </ Grid>
              </Paper>
            </ Container>
          </Box>

        </main>
          {/* Footer */}
          <Box sx={{ bgcolor: '#FAFAFA', p: 6 }} component="footer">
           <Typography variant="h6" align="center" gutterBottom>
             GodRoll
           </Typography>
           <Typography
             variant="subtitle1"
             align="center"
             color="text.secondary"
             component="p"
           >
             Let's build the Solana community. (v0.1.6)
           </Typography>
          </Box>
         {/* End footer */}
      </div>
    </ThemeProvider>
  );
}

export default App;
